import { Flex, Box, Image, Heading, GridItem, Grid } from "@chakra-ui/react";
import { useState } from "react";
import Switch from "./Switch";
import node from "./Utils/nodejs.png";
import github from "./Utils/github.png";
import redux from "./Utils/redux.png";
import cypress from "./Utils/cypress.png";
import chakraui from "./Utils/chakraui.png";
import express from "./Utils/express.png";

const Skills = () => {
  const Skills = [
    {
      Language: "HTML",
      image: "https://cdn-icons-png.flaticon.com/512/732/732212.png",
    },
    {
      Language: "CSS",
      image:
        "https://cdn.sanity.io/images/06yd5fkd/production/107574d887f1c07c9fbea0704de7f293e244a8f3-480x480.png",
    },
    {
      Language: "Javascript",
      image:
        "https://cdn.sanity.io/images/06yd5fkd/production/66bbf9242b1ccffebb5d46f376f5036b527fad48-480x480.png",
    },
    {
      Language: "React",
      image:
        "https://cdn.sanity.io/images/06yd5fkd/production/567ea6f25d34e2d6368538ee70b22726b1d7fd49-2300x2000.png",
    },
    {
      Language: "Redux",
      image: redux,
    },
    {
      Language: "ExpressJS",
      image: express,
    },
    {
      Language: "Chakra UI",
      image: chakraui,
    },
    {
      Language: "NodeJS",
      image: node,
    },
    {
      Language: "Github",
      image: github,
    },
    {
      Language: "MongoDB",
      image:
        "https://cdn.icon-icons.com/icons2/2415/PNG/512/mongodb_original_logo_icon_146424.png",
    },
    {
      Language: "TypeScript",
      image: "https://cdn-icons-png.flaticon.com/512/5968/5968381.png",
    },
    {
      Language: "Cypress",
      image: cypress,
    },
  ];

  const [isHovering, setIsHovering] = useState(true);

  // const [value, setValue] = useState(false);

  return (
    <Box w="90%" m="auto" pt="10%" mb="10%" id="skills">
      <Box
        w="20%"
        display="grid"
        gridTemplateColumns={{
          sm: "repeat(1, 1fr)",
          md: "repeat(1, 1fr)",
          lg: "repeat(2, 1fr)",
        }}
        gap="5"
        m="auto"
        mb="7%"
        justifyContent="center"
        textAlign="center"
      >
        <Box>
          <Heading
            pt="18%"
            fontSize={{ base: "4xl", sm: "4xl", md: "4xl", lg: "4xl" }}
            m="auto"
            textAlign="down"
          >
            Skills
          </Heading>
        </Box>
        <Box m="auto" h="10vh">
          <Switch
            isOn={isHovering}
            onColor="#94cf3a"
            handleToggle={() => setIsHovering(!isHovering)}
          />
        </Box>
      </Box>

      <Box
        display="grid"
        gridTemplateColumns={{
          sm: "repeat(1,1fr)",
          md: "repeat(2, 1fr)",
          lg: "repeat(2, 1fr)",
        }}
      >
        <Box m="auto">
          <Image
            w="70%"
            display="block"
            borderRadius="50%"
            m="auto"
            src="https://camo.githubusercontent."
          />
        </Box>
        <Grid
          m="auto"
          w="80%"
          templateColumns="repeat(3, 1fr)"
          gap="3"
          // onMouseEnter={() => setIsHovering(!isHovering)}
          // onMouseLeave={() => setIsHovering(false)}
        >
          {Skills.map((ele) => {
            return (
              <GridItem
                w="85%"
                m="auto"
                h={{ base: "auto", sm: "auto", md: "22vh", lg: "22vh" }}
                key={ele.image}
              >
                <Box>
                  <Image
                    w="90%"
                    m="auto"
                    mb="-20%"
                    // onMouseEnter={() => setIsHovering(!isHovering)}
                    // onMouseLeave={() => setIsHovering(false)}
                    src={ele.image}
                  />
                </Box>
                {isHovering && (
                  <Heading
                    w="110%"
                    fontSize={{ base: "15px", sm: "sm", md: "sm", lg: "lg" }}
                    borderRadius="10%"
                    mt="25%"
                  >
                    {ele.Language}
                  </Heading>
                )}
              </GridItem>
            );
          })}
        </Grid>
      </Box>
    </Box>
  );
};

export default Skills;
