import { Box, Flex, Heading, Image, Spacer, Text } from "@chakra-ui/react";
import React from "react";
import github from "./Utils/github.png"

const Project = () => {
  const projData = [
    {
      img: "https://github.com/Abhi11sep/ambiguous-heat-2587/raw/master/home.png",
      title: "Cartalane",
      desc: "Online Jewellery Store, where user can visits various jewellery, cart them and able to buy.",
      tech: "React | Chakra UI || ExpressJS || MongoDB",
      github: "https://github.com/Abhi11sep/ambiguous-heat-2587",
      netlify: "https://crystalgoldshop.netlify.app/",
    },
    {
      img: "https://iili.io/HxvNLZP.md.png",
      title: "Uboric Indiviual",
      desc: "Here at Uboric, we realize that style and prosperity begin with the correct shoes.",
      tech: "HTML | CSS | JavaScript",
      github: "https://github.com/Omchand27/Uboric-Clone-Individual",
      netlify: "https://thunderous-kitten-2a8a6c.netlify.app/index.html",
    },
    {
      img: "https://iili.io/HxkLw7I.png",
      title: "Uboric Clone",
      desc: "Here at Uboric, we realize that style and prosperity begin with the correct shoes.",
      tech: "HTML | CSS | JavaScript",
      github: "https://github.com/Omchand27/Uboric-Clone",
      netlify: "https://musical-parfait-580c0f.netlify.app/index.html#",
    },
  ];

  return (
    <Box id="projects" w="90%" pt="5%" m="auto">
      <Heading>Projects</Heading>
      <Box
        display="grid"
        gridTemplateColumns={{
          sm: "repeat(1, 1fr)",
          md: "repeat(2, 1fr)",
          lg: "repeat(3,1fr)",
        }}
        gap="8"
      >
        {projData.map((ele) => {
          return (
            <Box
              m="auto"
              p="3%"
              mt="3%"
              boxShadow="rgba(0, 0, 0, 0.35) 0px 5px 15px"
            >
              <Image h="30vh" src={ele.img} m="auto" />
              <Heading h="10vh">{ele.title}</Heading>
              <Text h="10vh" fontWeight="bold" textAlign="left">
                {ele.desc}
              </Text>
              <Text h="10vh" fontWeight="bold" textAlign="left">
                Tech Stack Used : {ele.tech}
              </Text>
              <Flex mt="3%">
                <Box>
                  <a target="_blank" href={ele.github}>
                    <Image
                      h="8vh"
                      src={github}
                    />
                  </a>
                </Box>
                <Spacer />
                <Box>
                  <a target="_blank" href={ele.netlify}>
                    <Image
                      h="8vh"
                      src="https://www.freeiconspng.com/thumbs/google-chrome-icon/google-chrome-icon-7.png"
                    />
                  </a>
                </Box>
              </Flex>
            </Box>
          );
        })}
      </Box>
    </Box>
  );
};

export default Project;
