import { Box, Image, Flex, Heading, Spacer, Button } from "@chakra-ui/react";
import resume from "../Download/resume.pdf";
import { AiFillHtml5 } from "react-icons/ai";
import {
  SiCss3,
  SiJavascript,
  SiRedux,
  SiExpress,
  SiNodedotjs,
  SiGithub,
  SiChakraui,
  SiMongodb,
  SiTypescript,
  SiCypress,
} from "react-icons/si";
import { DiReact } from "react-icons/di";
import pics from "./Utils/pics.png";
import github from "./Utils/github.png";

const Introduction = () => {
  return (
    <Box
      w="80%"
      m="auto"
      display="grid"
      gridTemplateColumns={{
        sm: "repeat(1, 1fr)",
        md: "repeat(2, 1fr)",
        lg: "repeat(2,1fr)",
      }}
      gap="8"
      pt="7%"
      id="about"
    >
      <Box>
        <Box w="60%" m="auto" borderRadius="5%" overflow="hidden">
          <Image display="block" m="auto" src={pics} alt="Omchand.jpg" />
        </Box>
      </Box>
      <Box textAlign="left">
        <Heading mt="15%" as="h1" size="2xl">
          Hello.
        </Heading>
        <Heading mt="5%" size="md">
          I'm Omchand Kanu.
        </Heading>
        <Heading mt="2%" size="md">
          Full Stack Web Developer, Enthusiast to Learn New FrameWorks. I have
          1200+ hours of coding experience.
        </Heading>
        <Flex h="7vh" mt="7%" w={{ sm: "80%", md: "60%", lg: "60%" }}>
          <Spacer />
          <a target="_blank" href="https://github.com/Omchand27">
            <Image
              h="7vh"
              transition="1"
              _hover={{
                boxShadow: "black 0px 5px 15px",
                transform: "scale(1.2)",
              }}
              borderRadius="50%"
              src={github}
            />
          </a>
          <Spacer />
          <a
            target="_blank"
            href="https://www.linkedin.com/in/omchand-kanu-542a3423a/"
          >
            <Image
              h="7vh"
              transition="1"
              _hover={{
                boxShadow: "blue 0px 5px 15px",
                transform: "scale(1.2)",
              }}
              borderRadius="50%"
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/f/f8/LinkedIn_icon_circle.svg/2048px-LinkedIn_icon_circle.svg.png"
            />
          </a>
          <Spacer />
          <a target="_blank" href="https://twitter.com/Omchand27">
            <Image
              h="7vh"
              transition="1"
              _hover={{
                boxShadow: "blue 0px 5px 15px",
                transform: "scale(1.2)",
              }}
              borderRadius="50%"
              src="https://www.freeiconspng.com/thumbs/logo-twitter-png/cricle-twitter-emblem-png-clipart-8.png"
            />
          </a>
          <Spacer />
          {/* <a href=""></a>
          <Image h="7vh" transition="1"
            _hover={{boxShadow: "red 0px 5px 15px", transform:"scale(1.2)"}} borderRadius="50%"
            src="https://www.shareicon.net/download/2015/09/26/107663_media_512x512.png"
          /> */}
        </Flex>
        <a href={resume} download="Omchand_Kanu_Resume">
          <Button
            mt="7%"
            mr="3%"
            bg="#fc1056"
            color="white"
            p="5% 7% 5% 7%"
            fontSize="2xl"
            _hover={{
              bg: "#fc1056",
              boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 16px",
            }}
          >
            Download CV
          </Button>
        </a>
        <a href="#contact">
          <Button
            mt="7%"
            bg="#fc1056"
            color="white"
            p="5% 10% 5% 10%"
            fontSize="2xl"
            _hover={{
              bg: "#fc1056",
              boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 16px",
            }}
          >
            <Box>Contact</Box>
          </Button>
        </a>
      </Box>
    </Box>
  );
};

export default Introduction;
